import React, { useState, useEffect } from 'react';
import { Link } from 'react-scroll';
import './Navbar.css'
import {Button} from './Button.js'
import logo  from './Logos/logo-white.png';
/*import logo from './dark-green-logo.png';*/
/**/

function Navbar() {


    const [click, setClick] = useState(false);
    const [button, setButton] = useState(true)

    const handleClick = () => setClick(!click);
    const closeMobileMenu = () => setClick(false);
    const showButton = () => {
        if(window.innerWidth <= 960) {
            setButton(false);

        } else {
            setButton(true);
        }
    };

    useEffect(() => {
        showButton()
    }, []);

    window.addEventListener('resize', showButton);


        return (
            <>
                <nav className="navbar">
                    <div className="navbar-container">
                        <Link to ="/" className="navbar-logo" onClick={closeMobileMenu}>
                            <img src={logo} alt='brand logo' className='navbar-logo'></img> 
                            <h1 className='nav-title'>TetraMeet</h1>     
                        </Link>
                        <div className="menu-icon" onClick={handleClick}> 
                            <i className={click ? 'fas fa-times': 'fas fa-bars' } />
                        </div>

                        <ul className={click ? 'nav-menu active' : 'nav-menu'}>
                            <li className='nav-item'>
                                <Link to='top' className='nav-links' onClick={closeMobileMenu} spy={true} smooth={true}>
                                    Home
                                </Link>
                            </li>
                            <li className='nav-item'>
                                <Link className='nav-links' onClick={closeMobileMenu} to='purpose' spy={true} smooth={true}>
                                    Product
                                </Link>
                            </li>
                            <li className='nav-item'>
                                <Link className='nav-links' onClick={closeMobileMenu} to='about-us' spy={true} smooth={true}>
                                    About Us
                                </Link>
                            </li>
                        </ul>
                    </div>
                </nav>
            </>
        );
}

export default Navbar
