import React from 'react';
import './Button.css';
import { Link } from 'react-router-dom';

const STYLES = ['btn--primary', 'btn--outline', 'btn--outline--navbar', 'btn--create-new-meeting',
'btn--submit', "btn-cm-exit", "platform-buttons", 'nm-submit-button'];

const SIZES = ['btn--medium', 'btn--large', 'btn--size--navbar', 'btn--size--dashboard-large',
'btn--large-submit', "btn-medium-exit", "platform-buttons-size", 'nm-submit-button-size'];


export const Button = ({
    children, 
    type, 
    onClick, 
    buttonStyle, 
    buttonSize,
    link
    }) => {
        const checkButtonStyle = STYLES.includes(buttonStyle) 
        ? buttonStyle 
        : STYLES[0];

        const checkButtonSize = SIZES.includes(buttonSize) 
        ? buttonSize 
        : SIZES[0];


        return(
            <Link to={link} className='btn-mobile'>
                <button
                className={`btn ${checkButtonStyle} ${checkButtonSize}`}
                onClick={onClick}
                type={type}
                >
                    {children}
                </button>
            </Link>
        )
        }